@font-face {
  font-family: "Helvetica";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Helvetica Regular"), url("../fonts/HelveticaRegular.woff2") format("woff2"), url("../fonts/HelveticaRegular.woff") format("woff");
}
@font-face {
  font-family: "Helvetica";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Helvetica Medium"), url("../fonts/HelveticaMedium.woff2") format("woff2"), url("../fonts/HelveticaMedium.woff") format("woff");
}
@font-face {
  font-family: "Helvetica";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Helvetica Bold"), url("../fonts/HelveticaBold.woff2") format("woff2"), url("../fonts/HelveticaBold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  margin: 0 auto;
  position: relative;
  min-height: 100%;
}
.container {
  margin: 0 auto;
}
@media (min-width: 1920px) {
  .container {
    width: 1110px;
    margin: 0 auto;
    padding: 0 365px;
  }
}
.page-header__wrapper {
  padding: 0 16px;
  display: flex;
  align-items: center;
  flex-direction: column;
  z-index: 1;
}
.page-header__inner {
  display: flex;
  flex-direction: column;
  margin: 0;
}
.page-header__image {
  width: 77px;
  height: 24px;
  display: none;
}
.page-header__nav {
  padding: 16px 0;
  display: flex;
  gap: 23px;
  justify-content: space-between;
}
.page-header__navigation {
  width: 100%;
  background-color: #ffffff;
}
.page-header__navigation-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.page-header__navigation-link {
  color: #9F9F9F;
  text-decoration: none;
  text-align: center;
  align-items: center;
  margin-top: 10px;
}
.page-header__navigation-link:active {
  color: #DEDEDE;
}
.page-header__navigation-link:hover {
  color: #000000;
}
.page-header__navigation-item {
  position: relative;
  margin: 10px 0;
}
.page-header__navigation-item::before {
  content: "";
  position: absolute;
  left: -20px;
  top: -5px;
  bottom: 0;
  height: 1px;
  background: #DEDEDE;
  width: calc(100% + 40px);
}
.page-header__navigation-item:last-child::after {
  content: "";
  position: absolute;
  left: -20px;
  top: 23px;
  bottom: 0;
  height: 1px;
  background: #DEDEDE;
  width: calc(100% + 40px);
}
.page-header__navigation--closed .page-header__navigation-item {
  display: none;
}
.page-header__navigation--opened .page-header__wrap {
  position: relative;
  top: -15px;
  right: 0;
  left: 0;
  min-height: 18vh;
  background-color: #ffffff;
  text-align: center;
}
.page-header__navigation--opened .page-header__wrap::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 7px;
  height: 100%;
}
.page-header__navigation--nojs .page-header__wrap {
  position: static;
  min-height: 0;
}
.page-header__navigation--nojs .page-header__navigation-item {
  display: block;
}
@media (min-width: 1920px) {
  .page-header__wrapper {
    padding: 0;
    display: flex;
  }
  .page-header__nav {
    padding: 30px 20px;
    gap: 0;
    display: flex;
    justify-content: space-between;
  }
  .page-header__navigation {
    padding: 0;
  }
  .page-header__logo {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .page-header__image {
    display: flex;
    width: 160px;
    height: 50px;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 1;
    left: 900px;
    top: 50px;
    margin: 0 auto;
  }
  .page-header__navigation-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 20px;
  }
  .page-header__navigation--closed .page-header__navigation-item {
    display: block;
  }
  .page-header__navigation-item {
    position: relative;
    margin: 0;
    font-size: 18px;
    font-weight: bold;
    line-height: 21px;
    text-transform: uppercase;
    flex-basis: 130px;
  }
  .page-header__navigation-item::before {
    content: "";
    position: absolute;
    display: none;
  }
  .page-header__navigation-item:nth-child(3) {
    margin-right: 100px;
  }
  .page-header__navigation-item:nth-child(4) {
    margin-left: 100px;
  }
  .page-header__navigation-item:nth-child(1),
  .page-header__navigation-item:nth-child(2),
  .page-header__navigation-item:nth-child(3) {
    text-align: left;
  }
  .page-header__navigation-item:nth-child(4),
  .page-header__navigation-item:nth-child(5),
  .page-header__navigation-item:nth-child(6) {
    text-align: right;
  }
  .page-header__navigation-item:last-child::after {
    content: "";
    position: absolute;
    display: none;
  }
}
.navigation {
  color: #000000;
}
.navigation__holder {
  display: flex;
  gap: 5px;
}
.navigation__list {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
  margin: 0;
}
.navigation__link {
  margin: 0 5px;
}
.navigation__input-holder {
  display: none;
}
.navigation__link--profile {
  display: none;
}
.navigation__link--menu {
  top: 4px;
  border: none;
  outline: none;
  background: none;
  padding: 0;
}
@media (min-width: 1920px) {
  .navigation__input-holder {
    display: block;
  }
  .navigation__input {
    padding: 0;
    margin: 0;
    border: none;
    border-bottom: 1px solid #9F9F9F;
  }
  .navigation__input::placeholder {
    font-family: "Helvetica", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 20px;
  }
  .navigation__link {
    margin: 0 10px;
  }
  .navigation__link--profile {
    display: block;
  }
  .navigation__link--logo {
    display: none;
  }
  .navigation__link--menu {
    display: none;
  }
}
.page-main__wrapper {
  padding: 0 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.page-main__holder {
  display: flex;
  align-items: flex-start;
  text-align: right;
  margin: 0;
}
.page-main__box {
  padding: 16px 0;
}
.page-main__title {
  margin: 0;
  font-size: 30px;
  font-weight: bold;
  line-height: 34px;
}
.page-main__wrap {
  display: flex;
  gap: 5px;
  margin: 12px 0 0 0;
}
.page-main__text {
  font-size: 16px;
  font-weight: normal;
  margin: 0;
}
.page-main__link {
  color: #9F9F9F;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
}
.page-main__link:active {
  color: #DEDEDE;
}
.page-main__link:hover {
  color: #000000;
}
@media (min-width: 1920px) {
  .page-main__wrapper {
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
  }
  .page-main__title {
    margin: 0;
    font-size: 40px;
    font-weight: bold;
    line-height: 46px;
  }
  .page-main__holder {
    padding: 20px 20px;
  }
  .page-main__box {
    padding: 20px 20px;
  }
}
.breadcrumb__holder {
  padding: 16px 0;
}
.breadcrumb__item {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 18px;
  font-weight: normal;
  line-height: 20px;
  text-decoration: none;
  color: #000000;
}
.breadcrumb__item:active {
  color: #DEDEDE;
}
.breadcrumb__item:hover {
  color: #000000;
}
.breadcrumb__item-main {
  color: #9F9F9F;
}
@media (min-width: 1920px) {
  .breadcrumb__holder {
    padding: 0;
  }
}
.form {
  padding: 16px 0;
}
.form__label {
  color: #9F9F9F;
  font-size: 12px;
  line-height: 14px;
}
.form__item--address {
  display: grid;
}
.form__input {
  font-size: 16px;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  height: 50px;
  width: 285px;
  padding: 0;
  color: #9F9F9F;
}
.form__input::placeholder {
  align-items: flex-start;
}
.form__item--select {
  position: relative;
  z-index: -1;
}
.form__item--select::before {
  content: "";
  top: 17px;
  right: 10px;
  position: absolute;
  display: block;
  background-image: url(../img/arrow.svg);
  background-repeat: no-repeat;
  z-index: 5;
  width: 10px;
  height: 10px;
  pointer-events: none;
}
.form__input--select {
  appearance: none;
  position: relative;
  z-index: -1;
}
.form__input--address {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  height: 50px;
  width: 285px;
  padding: 0;
  resize: none;
}
.form__input--address::placeholder {
  color: #000000;
}
.form__option {
  color: #9F9F9F;
  font-size: 12px;
  line-height: 14px;
}
@media (min-width: 1920px) {
  .form {
    padding: 20px 20px;
  }
  .form__image {
    display: none;
  }
  .form__input {
    font-size: 16px;
    border: none;
    border-bottom: 1px solid #9F9F9F;
    height: 50px;
    width: 515px;
  }
  .form__box {
    display: flex;
    justify-content: space-between;
  }
  .form__input--name,
  .form__input--phone {
    width: 235px;
  }
}
.cart {
  padding: 16px 0;
}
.cart__item {
  display: flex;
  gap: 12px;
  padding: 16px 0;
}
.cart__holder {
  display: flex;
  justify-content: space-between;
  gap: 32px;
}
.cart__title {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
}
.cart__image {
  width: 80px;
  height: 80px;
}
.cart__text {
  font-size: 26px;
  font-weight: bold;
  line-height: 30px;
  margin: 14px  0;
}
.cart__slogan {
  display: none;
}
.cart__wrapper-input {
  position: relative;
  margin-top: 25px;
  margin-bottom: 0;
}
.cart__input {
  width: 81px;
  height: 32px;
  font-weight: bold;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  align-items: center;
  border: 1px solid #000000;
  outline: none;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 25px;
}
.cart__input::placeholder {
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  color: #000000;
}
.cart__button {
  position: absolute;
  border: none;
  background: none;
  outline: none;
  padding: 0;
}
.cart__button-reduce {
  width: 12px;
  height: 0;
  top: 8px;
  left: 5px;
}
.cart__button-increase {
  width: 12px;
  height: 12px;
  top: 10px;
  right: 10px;
}
.cart__product-price {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  text-align: right;
}
.cart__product-container {
  display: flex;
  flex-direction: column;
  justify-content: left;
}
.cart__button-delete {
  font-size: 12px;
  color: #9F9F9F;
  margin-top: 50px;
  text-align: right;
}
.cart__button-delete:active {
  color: #DEDEDE;
}
.cart__button-delete:hover {
  color: #000000;
}
.cart__total-holder {
  display: flex;
  justify-content: space-between;
}
.cart__total-text {
  font-size: 26px;
  line-height: 30px;
  font-weight: bold;
}
.cart__button-link {
  width: 286px;
  height: 60px;
  background: none;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  line-height: 25px;
  border: 1px solid #000000;
}
.cart__button-link:hover {
  border: 1px solid #9F9F9F;
  color: #9F9F9F;
}
.cart__button-link:active {
  border: 1px solid #DEDEDE;
  color: #DEDEDE;
}
@media (min-width: 1920px) {
  .cart {
    padding: 20px 20px;
  }
  .cart__item:first-child::after {
    content: "";
    position: absolute;
    display: none;
  }
  .cart__item {
    padding: 0;
    margin: 34px 0;
    gap: 24px;
    position: relative;
  }
  .cart__item::after {
    content: "";
    position: absolute;
    left: 0;
    top: -19px;
    bottom: 0;
    height: 1px;
    background: #DEDEDE;
    width: 100%;
  }
  .cart__title {
    font-size: 20px;
  }
  .cart__slogan {
    display: block;
    color: #9F9F9F;
    font-size: 16px;
  }
  .cart__total-text {
    font-size: 28px;
  }
  .cart__product-price {
    font-size: 20px;
  }
  .cart__image-holder {
    margin: 0;
    padding: 0;
  }
  .cart__image {
    width: 150px;
    height: 150px;
  }
  .cart__input {
    margin-top: auto;
    margin-bottom: 0;
  }
  .cart__button-delete {
    margin-top: auto;
  }
  .cart__button-increase {
    width: 12px;
    height: 12px;
    top: 10px;
    left: 62px;
  }
  .cart__total-holder {
    display: none;
  }
  .cart__button-link {
    width: 503px;
    height: 60px;
    background: none;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    line-height: 25px;
    border: 1px solid #000000;
  }
  .cart__button-link:hover {
    border: 1px solid #9F9F9F;
    color: #9F9F9F;
  }
  .cart__button-link:active {
    border: 1px solid #DEDEDE;
    color: #DEDEDE;
  }
}
.map {
  display: none;
}
@media (min-width: 1920px) {
  .map {
    display: block;
  }
  .map__image {
    display: flex;
  }
}
